<!--个人页面-->
<template>
  <div>
    头像URL: {{userData.userHead}}<br>
    用户名: {{userData.userName}}<br>
    用户手机号: {{userData.userPhone}}<br>
    是否学生认证: {{userData.studentAuthentication}}
    <Footer/>
  </div>
</template>

<script>
  import Footer from '../components/Footer'

  export default {
    name: 'userdata',
    components: {Footer},
    data () {
      return {
        //用户登录标识
        loginCookie: '',
        //用户信息
        userData: {
          //用户头像url
          userHead: '',
          // 昵称
          userName: '麦田里的风',
          // 手机号
          userPhone: '19927457507',
          // 是否学生认证
          studentAuthentication: false
        }
      }
    },
    methods: {},
    created () {
      const that = this
      that.loginCookie = that.$cookie.get('token')
      //请求获取个人信息
      that.$http.get('/i', {
        params: {
          Cookie: that.$cookie.get('token')
        }
      })
        .then(function (response) {
          if (200 === response.data.errorCode) {
            //将请求到的数据写到用户信息中
            that.userData = response.data.data
          }
        })
        .catch(function (error) {
          console.log(error)
        })
        .then(function () {
          // always executed
        })

    },

  }
</script>

<style scoped>

</style>



